/*slider*/
.switch {
  position: relative;
  width: 40px;
  height: 20px;
  right: -14px;
  bottom: -2px;
  display: inline-block;
}
.switch input {
  height: 0;
  width: 0;
  opacity: 0;
}
.slider {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  cursor: pointer;
  background-color: #ccc;
  border-radius: 10%;
  transition: 0.5s;
}
.slider::before {
  position: absolute;
  content: "";
  height: 15px;
  width: 15px;
  background-color: black;
  left: 4px;
  bottom: 2.5px;
  border-radius: 50%;
  transition: 0.5s;
}
input:checked + .slider {
  background-color: #ccc;
}
input:checked + .slider:before {
  background-color: black;
}
input:focus + .slider {
  box-shadow: 0 0 1px black;
}
input:checked + .slider:before {
  -webkit-transform: translateX(15px);
          transform: translateX(15px);
}

